// 
import Vue from "vue";
import Vuex from "vuex";
import mutations from "./mutations.js";
import actions from "./actions.js";

// 
Vue.use(Vuex);

// Store
const store=new Vuex.Store({
    // 
    state:{
        // 
        topics:[
            {name:'all',desc:''},
            {name:'good',desc:''},
            {name:'share',desc:''},
            {name:'ask',desc:''},
            {name:'job',desc:''},
            {name:'dev',desc:''}
          ],
          //
          unLoginedmoreMenus:{
            login:''
          },
          //
          LoginedmoreMenus:{
            create:'',
            center:'',
            msg:'',
            logout:''
          },
          // 
          isLogined:false,
          // 
          loginname:'',
          // id
          author_id:'',
          //accesstoken
          accesstoken:'' ,
          // 
          isShowMoreMenus:false
    },
    // 
    getters:{
      moreMenus:function(state){
        if (state.isLogined){
          //
          return state.LoginedmoreMenus;
        }
        return state.unLoginedmoreMenus;
      },
    },
    mutations,
    actions
});

// 

export default store;


